<template>
    <div class="nav-container">
        <div class="header-section">
            <h1 class="title">博·客·系·统</h1>
            <span class="designer">欢迎{{ username }}</span>
        </div>
        <el-menu class="nav-menu" mode="horizontal" :default-active="activeIndex" @select="handleSelect">
            <el-menu-item index="home">首页</el-menu-item>
            <el-menu-item index="personal">个人主页</el-menu-item>
            <el-menu-item index="friends">好友</el-menu-item>
            <el-menu-item index="game">游戏</el-menu-item>
            <el-menu-item index="about">关于我们</el-menu-item>
            <el-menu-item index="exit">退出</el-menu-item>
        </el-menu>
        <div class="blog-container">
            <el-card v-for="blog in extractedData" :key="blog.id" class="blog-card">
                <template #header>
                    <h3>{{ blog.title }}</h3>
                    <h4>{{ blog.username }}</h4>
                    <span class="blog-date">{{ new Date(blog.date).toLocaleString('zh-CN', {
                        year: 'numeric',
                        month: '2-digit',
                        day: '2-digit',
                        hour: '2-digit',
                        minute: '2-digit',
                        second: '2-digit'
                    }) }}</span>
                </template>
                <div class="blog-content">
                    {{ blog.content }}
                </div>
                <!-- 使用 ElementPlus 的按钮组件 -->
                <el-button type="danger" @click="deleteBlog(blog.date)">删除</el-button>
                <!-- 新增评论展示区域，包裹在折叠组件中 -->
                <el-collapse v-bind:model-value="isCommentCollapsed[index].toString()" accordion>
                    <el-collapse-item title="评论区">
                        <div v-for="(comment, index) in getCommentsForBlog(blog.date)" :key="index"
                            class="comment-item">
                            <div class="comment-content">{{ comment.content }}</div>
                            <span class="comment-date">{{ new Date(comment.date).toLocaleString('zh-CN', {
                                year: 'numeric',
                                month: '2-digit',
                                day: '2-digit',
                                hour: '2-digit',
                                minute: '2-digit'
                            }) }}</span>
                        </div>
                    </el-collapse-item>
                </el-collapse>
            </el-card>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
import apiUrl from '../config.js';
import { ElCollapse, ElCollapseItem } from 'element-plus';

const activeIndex = ref('personal'); // 设置默认选中项
const sortedBlogs = ref([]); // 存储排序后的博客数据
const extractedData = ref([]);
const allComments = ref([]);
const isLoggedIn = ref(false);
const userInfo = ref({});
const username = ref('');
const router = useRouter();
// 用于存储每个博客评论区的折叠状态
const isCommentCollapsed = ref({});

const handleSelect = (index) => {
    console.log('当前选中：', index);
    if (index === 'home') {
        router.push('/home');
    }
    if (index === 'exit') {
        // 可在此处添加退出登录等逻辑
    }
    if (index === 'get') {
        fetchUserBlogs(); // 调用获取博客数据的函数 
    }
};

const fetchUserBlogs = async () => {
    try {
        const response = await axios.get(`${apiUrl}/api/personBlogs/${username.value}`);
        sortedBlogs.value = response.data.a;
        extractedData.value = sortedBlogs.value.map((blogItem) => ({
            date: blogItem.date,
            title: blogItem.title,
            content: blogItem.content,
            username: blogItem.username,
        }));
        // 初始化每个博客评论区的折叠状态为折叠
        extractedData.value.forEach(blog => {
            isCommentCollapsed.value[blog.id] = true;
        });
        // 新增获取评论的调用
        await fetchAllComments();
    } catch (error) {
        console.log('获取博客失败:', error);
    }
};

const fetchAllComments = async () => {
    try {
        const response = await axios.get(`${apiUrl}/api/getComments`);
        allComments.value = response.data;
    } catch (error) {
        console.error('获取评论失败:', error);
    }
};

const getCommentsForBlog = computed(() => (blogDate) => {
    if (!allComments.value) return [];
    return allComments.value.filter(comment => comment && comment.blogDate === blogDate);
});

const deleteBlog = async (date) => {
    try {
        await axios.delete(`${apiUrl}/api/deleteBlogsByTime`, {
            data: {
                date: date
            }
        });
        // 删除成功后，更新前端数据
        extractedData.value = extractedData.value.filter(blog => blog.date !== date);
        // 同时删除被删除博客对应的折叠状态
        const blogIdToRemove = extractedData.value.find(blog => blog.date === date)?.id;
        if (blogIdToRemove) {
            delete isCommentCollapsed.value[blogIdToRemove];
        }
    } catch (error) {
        console.error('删除博客失败:', error);
    }
};

onMounted(async () => {
    try {
        username.value = localStorage.getItem('username');
        fetchUserBlogs();
    } catch (error) {
        console.error('验证登录状态出错:', error);
    }
});
</script>

<style scoped>
.nav-container {
    background: #e3e9f3;
    padding: 15px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.header-section {
    text-align: center;
    margin-bottom: 20px;
}

.title {
    font-size: 28px;
    color: #4CAF50;
    margin: 0;
    font-weight: 600;
}

.designer {
    font-size: 14px;
    color: #6c757d;
    margin-top: 5px;
    display: block;
}

.nav-menu {
    justify-content: center;
    gap: 12px;
}

.el-menu-item {
    padding: 12px 22px;
    border-radius: 8px;
    font-size: 16px;
}

.blog-container {
    padding: 20px;
}

.blog-card {
    margin-bottom: 20px;
}

.blog-date {
    float: right;
    color: #999;
}

.blog-content {
    margin-top: 10px;
}

.comment-section {
    margin-top: 10px;
    padding: 10px;
    border-top: 1px solid #ccc;
}

.comment-item {
    margin-bottom: 5px;
}
</style>